import React from 'react';
import './BaseExpand.css';
import Ceges_Mobil from '../../../assets/Card/Ceges_Mobil.png';

const BaseExpand = props =>{

  let changing_class = 'CloseSidedrawerCeg';

  if (props.boolean_open_sidedrawer) {
    changing_class = 'OpenSidedrawerCeg';
  }

    return(
      <div className="BaseExpandEgeszCeg">

              <div className="ButtonKontenerCeg">
                <button className="ButtonCeg" onClick= {props.click}>Rszletek</button>
              </div>
              
              <div className={changing_class}>
                  Azon cgeknek, akik szeretnk modernizlni vllalkozsukat, 
                  megtartani a versenykpessgket s nem elgszenek meg csupn
                  az alapszolgltatsokkal. Ehhez a kvetkezket nyjtjuk:

                <ul className="FelsorolCeg">
                  <li><span>Weblapja kiemelkedik a tbbi kzl tartalmban s megjelensben</span></li>        
                  <li><span>Azonnali zenetklds lehetsgt gyfeleinek</span></li>
                  <li><span>Szvegrst s cges logt</span></li>
                  <li><span>Meghosszabbtott ingyenes karbantartst s tbb profi kpet</span></li>
                  <li><span>Weblapjt sszekapcsoljuk facebook oldalval</span></li>     
                </ul>                
                    
              </div>

              <div className="KepStartCeg">
                <img src={Ceges_Mobil} alt='pipa'/>
              </div>

        </div>  
    )
}
export default BaseExpand;