import React from 'react';
import './BaseExpand.css';
import Bemutatkozo_Mobil from '../../../assets/Card/Bemutatkozo_Mobil.jpg';


const BaseExpand = props =>{

  let changing_class = 'CloseSidedrawerBem';

  if (props.boolean_open_sidedrawer) {
    changing_class = 'OpenSidedrawerBem';
  }

    return(
     <div className="BaseExpandEgeszBem">

        <div className="ButtonKontenerBem">
          <button className="ButtonBem" onClick= {props.click}>Rszletek</button>
        </div>
        
        <div className={changing_class}>        
      
          J vlaszts lehet terletileg korltozott,
          vagy alacsony kltsgvets vllakozsok szmra.          
          Kivlan alkalmas pldul a kvetkezkre:    

          <ul className="FelsorolBem">
            <li><span>Potencilis gyfelei tjkoztatsra</span></li>
            <li><span>Vllalkozsa sznvonalnak nvelsre</span></li>
            <li><span>Termkei npszerstsre</span></li>
            <li><span>Vllalkozsa online referenciapontjnak</span></li>
            <li><span>Munkaminti bemutatsra</span></li>
          </ul>        
        </div>

        <div className="KepStartBem">
          <img src={Bemutatkozo_Mobil} alt='pipa'/>
        </div>

    </div>
    )
}
export default BaseExpand;