import React  from 'react';
import './Ajanlas.css';
import '../form/MyForm';
import ajanlas from '../../assets/Ajanlas/Ajanlas.jpg';
import { HashLink as Link } from 'react-router-hash-link';

const Ajanlas= props => { 
      
      return (
            <div  className='AjanlasEgesz'>  
                <div className='AjanlasCim'>
                    <h1>Ajnljon engem s keressen pnzt!</h1>
                </div>

                <div className="AjanlasKontener">
                  
                        <div className="AjanlasSzoveguj">
                        Itt van  cgnk koronavrusra adott vlasza.
                        Ha az ajnlsbl kifolylag sikeres zlet lesz
                        akkor kifizetem nnek:
                        <ul>
                            <li>A teljes r 10% -t a Bemutatkoz s
                                Cges csomag esetben.</li>
                            <li>Illetve 5% -t az gyflszerz csomag esetben.</li>
                        </ul> 
                         (A sikeres zlet itt azt jelenti, hogy elkszlt s kifizetve.
                           Az ajnls tnyt mind nnek mind a megrendelnek jeleznie kell.)
                        </div>                 

                    <div className="AjanlasKep">
                        <img src={ajanlas} alt="Ajanlas-kep" ></img>
                    </div>     

                </div>

                <div className="AjanlomGomb">
                    <div className="AjanlomGomb2">
                        <Link to={{         
                        pathname: "/Kapcsolat",      
                        hash: "#form-1",
                        }}> <h1>Ajnlok!</h1></Link>   
                    </div>
              </div>      
            </div>
      )
}
export default Ajanlas;